.wrapper {
    background: #FFFFFF;

    .container {
        display: flex;
        flex-wrap: wrap;

        .left-2 {
            width: calc(100% - 650px);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 0 45px;

            h2 {
                font-family: 'Gilroy bold', sans-serif;
                font-weight: bold;
                font-size: 24px;
                text-align: center;
                text-transform: uppercase;
            }

            #email-input {
                width: 100%;
            }

            input {
                width: 100%;
                outline: none;
                background: #FFFFFF;
                border: 1px solid rgba(151, 151, 151, 0.482592);
                border-radius: 5px;
                font-size: 19px;
                padding: 20px 25px;
            }

            label {
                font-weight: 500;
                font-size: 17px;
                color: #787993;
                margin-left: 0;
            }

            .line {
                margin-top: 25px;

                .input-wrapper {
                    display: flex;

                    input {
                        &:first-of-type {
                            width: 35%;
                            margin-right: 30px;
                        }
                    }
                }
            }

            .about {
                text-align: center;
                font-size: 12px;
                line-height: 14px;
                margin: 30px 0;

                .primary {
                    color: #304FFF;
                }
            }

            .button-group {
                display: flex;
                align-items: center;
                justify-content: space-between;

                .back {
                    background: #FFFFFF;
                    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.279127);
                    border-radius: 5px;
                    padding: 20px 35px;
                    display: inline-block;
                }

                .next {
                    padding: 20px 48px;
                    background: #41D481;
                    border-radius: 5px;
                    text-decoration: none;
                    font-size: 17px;
                    color: #FFFFFF;
                }
            }
            .bottom {
                text-align: center;
                width: 100%;
                color: #787993;
                position: relative;

                span {
                    &::before {
                        content: '';
                        width: 100%;
                        top: 0;
                        bottom: 0;
                        width: 45%;
                        position: absolute;
                        height: 2px;
                        background: #787993;
                        left: 0;
                        margin: auto 0;
                    }

                    &::after {
                        content: '';
                        width: 100%;
                        top: 0;
                        bottom: 0;
                        width: 45%;
                        position: absolute;
                        height: 2px;
                        background: #787993;
                        right: 0;
                        margin: auto 0;
                    }
                }
            }
        }

        .right {
            width: 650px;
            height: 950px;
            background: url(../../assets/images/Login-bg.png) no-repeat;
            background-size: cover;
            position: relative;
            z-index: 1;

            .extra {
                width: 275px;
                height: 275px;
                background: url(../../assets/images/login-small.svg) no-repeat;
                background-size: cover;
                position: absolute;
                bottom: 185px;
                left: 60px;
                padding: 79px 42px 0 42px;

                p {
                    font-family: 'Gilroy bold', sans-serif;
                    font-weight: bold;
                    font-size: 19px;
                    color: #787993;
                    margin: 7px 0 18px;
                }

                a {
                    display: inline-block;
                    text-decoration: none;
                    color: #41D481;
                    padding: 9px 18px;
                    border: 2px solid #41D481;
                    border-radius: 5px;
                    font-weight: bold;
                    font-size: 16px;
                    font-family: 'Gilroy bold', sans-serif;
                }
            }
        }
    }
}